.content-info {
  .row {
    margin-bottom: 0;
  }

  a {
    transition: 0.2s;

    &:hover, &:focus {
      background: $aqua-light;
      text-decoration: none;
    }

    &:active {
      color: $navy;
      background: $shadow;
    }
  }
}

.top-border {
  background-color: $ice;
  border-bottom: 1px solid $navy;
  border-top: 1px solid $navy;
  height: 10px;
  margin-bottom: 50px;
}

.bottom-bar {
  background-color: $ice;
  border-top: 1px solid $navy;
  font-size: 12px;
  padding: 10px 0 5px;
}

.footer-logo {
  font-size: 12px;
  font-family: $font-family-sans-serif;
}

.svg-unity-logo {
  height: 22px;
}

.social-media a, .footer-menu a {
  color: $navy;
  font-family: $font-family-sans-serif;
  font-size: 16px;
}

.footer-logo {
  p {
    margin: 0;
  }

  svg {
    max-width: 100%;
  }
}



